.header-top {
  height: 38px;
  background: #121212;
}

.header-top .inner {
  height: 100%;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 784px) {
  .header-top {
    display: none;
  }
}

.inner {
  width: 100%;
  max-width: 1170px;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
}

@media (max-width: 1150px) {
  .inner {
    max-width: 780px;
  }
}

.ht-btns a {
  letter-spacing: .005em;
  color: #fff;
  margin-right: 15px;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
}

.ht-socials {
  letter-spacing: .005em;
  color: #fff;
  flex-wrap: wrap;
  align-items: center;
  font-size: 14px;
  line-height: 18px;
  display: flex;
}

.ht-socials span {
  margin-right: 15px;
}

.hts-icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 12px;
}

.hts-icon:last-child {
  margin-right: 0;
}

.hts-vk-icon {
  width: 22px;
  height: 12px;
  background-image: url("vk-icon.63049b91.svg");
}

.hts-tg-icon {
  width: 16px;
  height: 16px;
  background-image: url("tg-icon.5d90b4a4.svg");
}

.hts-insta-icon {
  width: 16px;
  height: 16px;
  background-image: url("insta-icon.55fc7642.svg");
}

.header {
  padding: 15px 0;
}

.header .inner {
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 1150px) {
  .header .inner {
    justify-content: center;
  }
}

@media (max-width: 784px) {
  .header {
    display: none;
  }
}

.h-logo {
  text-transform: uppercase;
  color: #242526;
  font-size: 32px;
  font-weight: 900;
  line-height: 32px;
  text-decoration: none;
}

.h-logo span {
  color: #2f8f41;
}

@media (max-width: 1150px) {
  .h-logo {
    padding: 33px 0;
  }
}

.h-banner img {
  max-width: 100%;
  display: block;
}

.header-menu {
  height: 44px;
  background: #f5f5f5;
  border: 1px solid #e5e5e5;
}

.header-menu .inner {
  height: 100%;
  justify-content: space-between;
  align-items: center;
}

.header-menu ul {
  height: 100%;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.header-menu li {
  margin-right: 26px;
  list-style: none;
}

.header-menu li a {
  letter-spacing: .005em;
  text-transform: uppercase;
  color: #212121;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
}

@media (max-width: 784px) {
  .header-menu {
    display: none;
  }
}

.hm-btns {
  height: 100%;
  flex-wrap: wrap;
  align-items: center;
  display: flex;
}

.hmb-btn {
  height: 32px;
  letter-spacing: .005em;
  border-radius: 3px;
  align-items: center;
  margin-right: 8px;
  padding: 0 12px;
  font-size: 15px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  display: flex;
}

.hmb-btn:last-child {
  margin-right: 0;
}

.hmb-btn:before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin-right: 7px;
}

.hmb-btn-yt {
  color: #fff;
  background: #cd201f;
}

.hmb-btn-yt:before {
  width: 23px;
  height: 16px;
  background-image: url("yt-icon.0d8be3a4.svg");
}

.hmb-btn-gp {
  color: #242526;
  background: #f5f5f5;
  border: 1px solid #9a9d9f;
}

.hmb-btn-gp:before {
  width: 18px;
  height: 20px;
  background-image: url("gplay-icon.92efa8ac.svg");
}

@media (max-width: 1150px) {
  .hmb-btn-gp {
    display: none;
  }
}

.hmb-btn-search {
  background: #2f8f41;
  padding: 0 7px;
}

.hmb-btn-search:before {
  width: 20px;
  height: 20px;
  background-image: url("search-icon.40a92807.svg");
  margin-right: 0;
}

.main-page-banner {
  background-image: url("main-banner.443a8582.jpeg");
  background-position: center;
  background-size: cover;
  padding-top: 20px;
  padding-bottom: 30px;
  position: relative;
}

.main-page-banner h2 {
  text-align: center;
  color: #fff;
  width: 100%;
  margin: 0 0 20px;
  font-size: 38px;
  font-weight: 700;
  line-height: 46px;
}

.main-page-banner p {
  text-align: center;
  color: #dfe4e6;
  width: 100%;
  margin: 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
}

@media (max-width: 784px) {
  .main-page-banner h2 {
    font-size: 32px;
  }
}

@media (max-width: 700px) {
  .main-page-banner {
    max-height: 260px;
    overflow-y: hidden;
  }
}

.items-tabs {
  height: 65px;
  background: #f5f5f5;
}

.items-tabs .inner {
  height: 100%;
  justify-content: center;
  align-items: center;
}

@media (max-width: 1150px) {
  .items-tabs {
    height: 45px;
    padding: 0;
  }

  .items-tabs .inner {
    max-width: unset;
    justify-content: flex-start;
    padding: 0;
  }
}

@media (max-width: 888px) {
  .items-tabs .inner {
    flex-wrap: unset;
    overflow-x: scroll;
  }
}

.items-tab {
  text-align: center;
  color: #3f4143;
  background: #e3e4e5;
  border: 1px solid #b2b7ba;
  border-radius: 5px;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 7.5px;
  padding: 8px 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  text-decoration: none;
  transition: all .5s;
  display: flex;
}

.items-tab:hover {
  background: rgba(30, 136, 229, .1);
  border: 1px solid #1e88e5;
}

@media (max-width: 1150px) {
  .items-tab {
    height: 45px;
    border-right: 0;
    border-radius: 0;
    margin: 0;
    padding: 8px 20px;
  }

  .items-tab:last-child {
    border-right: 1px solid #b2b7ba;
  }
}

@media (max-width: 888px) {
  .items-tab {
    white-space: nowrap;
  }
}

.items-tab-selected {
  opacity: .5;
}

.items-tab-wm {
  min-width: 162px;
}

.items-tab-wm:before {
  content: "";
  width: 18px;
  height: 18px;
  background-image: url("webmoney-icon.934c0905.png");
  margin-right: 8px;
}

.items-tab-ym {
  min-width: 192px;
  flex-wrap: wrap;
  align-items: center;
  display: flex;
}

.items-tab-ym:before {
  content: "";
  width: 17px;
  height: 24px;
  background-image: url("yandex-icon.3975c181.png");
  margin-right: 8px;
}

.items {
  padding: 33px 0;
}

@media (max-width: 752px) {
  .items {
    padding-top: 20px;
  }
}

.item {
  flex-wrap: wrap;
  margin-bottom: 30px;
  display: flex;
}

@media (max-width: 752px) {
  .item {
    position: relative;
  }
}

.item-img-container {
  max-width: 230px;
}

.item-img-container img {
  width: 100%;
  display: block;
}

.item-img {
  position: relative;
}

.item-img:after {
  width: 100%;
  height: 100%;
  content: "";
  background: linear-gradient(rgba(6, 8, 9, 0) 75%, #000 99.92%, #060809 99.93%, rgba(204, 204, 204, .686) 99.94%, rgba(177, 177, 177, .73) 99.95%, #060809 99.96%, rgba(0, 0, 0, .533) 99.97%, #060809 99.98%);
