    background: #fafafa;
    border: 1px solid #e0e0e0;
    border-right: 0;
    padding-top: 55px;
    padding-bottom: 21px;
    padding-left: 20px;
  }

  .item-img:after {
    display: none;
  }

  .item-img img {
    max-width: 100px;
  }
}

.items-list {
